import React from 'react'
import DeleteIcon from '@material-ui/icons/Delete';

export const TodoItem = ({ todo, onDelete, mode }) => {
    return (
        <>
            <div>
                <h4>{todo.title}</h4>
                <p>{todo.desc}</p>
                <div className="form-check">
                    <input className="form-check-input" type="checkbox" value="" id="defaultCheck1" style={{ cursor: 'pointer' }} data-bs-toggle="tooltip" data-bs-placement="top" title="Mark/unmark as done/completed" />
                    <label className="form-check-label" htmlFor="defaultCheck1" style={{ cursor: 'pointer', color: mode === 'primary' ? 'green' : 'gold' }}>
                        Mark as done or completed.
                    </label>
                </div>
                <button className="btn btn-sm btn-danger my-3" data-bs-toggle="tooltip" data-bs-placement="bottom" title="Delete a todo item" onClick={() => { onDelete(todo) }}><DeleteIcon /></button>
            </div>
            <hr />
        </>
    )
}
